// * for ".dot"
function currentSlide(n) {
  showSlides((slidePosition = n));
}

function showSlides(n) {
  console.log("üöÄ ~ file: slider-hero.js ~ line 13 ~ showSlides ~ n", n);
  let i;
  const slides = document.getElementsByClassName("fade");
  console.log(
    "üöÄ ~ file: slider-hero.js ~ line 16 ~ showSlides ~ slides",
    slides
  );
  const dot = document.getElementsByClassName("dot");
  console.log("üöÄ ~ file: slider-hero.js ~ line 16 ~ showSlides ~ dot", dot);

  // * –µ—Å–ª–∏ slidePosition > –¥–ª–∏–Ω—ã –ø—Å–µ–≤–¥–æ–º–∞—Å—Å–∏–≤–∞ slides
  if (n > slides.length) {
    slidePosition = 1;
  }
  // * –µ—Å–ª–∏ slidePosition < 1 => –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –ø–æ–∑–∏—Ü–∏—é –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
  if (n < 1) {
    slidePosition = slides.length;
  }

  for (i = 0; i < slides.length; i = i + 1) {
    slides[i].style.display = "none";
  }
  for (i = 0; i < dot.length; i = i + 1) {
    dot[i].className = dot[i].className.replace("active", "");
  }

  slides[slidePosition - 1].style.display = "block";
  console.log(
    "üöÄ ~ file: slider-hero.js ~ line 40 ~ showSlides ~ slidePosition",
    slidePosition
  );
  dot[slidePosition - 1].className += " active";
}
